body {
  margin: 0;
  padding: 0;
  background-color: #0d0d0d;
  font-family: "Red Hat Display", sans-serif;
}

html {
  scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
}

section {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  scroll-snap-align: start;
}
